import { useEffect, useState } from "react"
import { csv2json } from "json-2-csv"
import Chart from "./Chart"
import "./App.css"

function App() {
  const [data, setdata] = useState()
  const [cities, setCities] = useState()
  useEffect(() => {
    const fetchData = async () => {
      const water = await fetch(
        "https://www.data.gouv.fr/fr/datasets/r/2963ccb5-344d-4978-bdd3-08aaf9efe514"
      ).then((res) => res.text())
      setdata(
        csv2json(water, {
          delimiter: { field: ";" },
        })
      )
    }
    const fetchCities = async () => {
      const data = await fetch(
        "https://www.data.gouv.fr/fr/datasets/r/dd9cf705-a759-46c6-afd6-bc85cf25f363"
      ).then((res) => res.text())
      setCities(
        csv2json(data, {
          delimiter: { field: ";" },
        })
      )
    }
    fetchData()
    fetchCities()
  }, [])

  return (
    <div className="App">
      <header>
        <h1>Surveillance du Sars-CoV-2 dans les eaux usées</h1>
        <p>
          Un graphique en temps réel de la surveillance de la concentration de
          Sars-CoV-2 dans les eaux usées. C'est un indicateur de l'intensité des
          vagues de circulation virale pour le suivi de pandémie de Covid-19 en
          France.
        </p>
      </header>
      <Chart data={data} cities={cities} />
      <h2>A propos</h2>
      <p>
        {" "}
        Le dispositif français de surveillance des eaux usées{" "}
        <abbr title="Dispositif français de surveillance des eaux usées">
          SUM’Eau
        </abbr>{" "}
        a été initié sous l’impulsion de la direction générale de la santé et de
        la direction de l’eau et de la biodiversité avec l’appui technique et
        scientifique des agences sanitaires. La surveillance des eaux usées
        poursuit deux objectifs, qui sont de détecter précocement la présence du
        génome du SARS-CoV-2 en population générale et de suivre les tendances
        de la circulation virale au sein de la population.
      </p>
      <p>
        ✔️ Ces données sont actualisées en <strong>temps réel</strong> à partir
        de{" "}
        <a href="https://www.data.gouv.fr/fr/datasets/surveillance-du-sars-cov-2-dans-les-eaux-usees-sumeau/">
          Datagouv
        </a>
        . Les données de Sum'Eau sont livrées à Datagouv avec une à deux
        semaines de décalage, donc gardez à l'esprit que c'est un indicateur de
        la circulation virale de la semaine dernière.
      </p>
    </div>
  )
}

export default App
